@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Lato', sans-serif;
  text-align: center;
}

p {
  margin: 0px;
  padding: 0px;
}

img { 
  max-width:100%; 
  max-height:100%; 
} 

/*************** HOME ****************/

.Home {
  width: 100vw;
  height: 100vh;
  background-color: #F2F2F2;
  display: inline-block;

}

.home_page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.outer-page {
  height: 10%;
  width: 90%;
}

.my_info {
  text-align: left;
  width: 50%;
  position: relative;
  top: 40vh;
  left:10%;
}

.name {
  font-weight: bold;
  font-size: 4vw;
  margin-top: 0px;

}

.container {
  position: absolute;
  right: 0px;
  top:0px;
  height: 100vh;
  width: 45vw;
  text-align: center;
}


.image {
  object-fit: fill;
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
}

.overlay {
  visibility: hidden;
  opacity: 0;
  height: 100%;
  width: 100%;
  -webkit-transition: .5s ease;
  transition: .5s ease;

}

.container:hover .overlay {
  opacity: 1;
  visibility: visible;
}

.container:hover .image {
  -webkit-filter:opacity(.3);
          filter:opacity(.3);
  -webkit-transition: .5s ease;
  transition: .5s ease;
}

.text {
  margin-top:20%;
  font-size: 1.5vw;
  display: inline;
  margin: 5%;
  top: 5%;
  position: relative;
  text-align: center;
  -webkit-transition: .3s ease;
  transition: .3s ease;

}

.text-selected {
  margin-top:20%;
  font-size: 1.5vw;
  -webkit-filter: opacity(.4);
          filter: opacity(.4);
  display: inline;
  margin: 5%;
  top: 5%;
  position: relative;
  text-align: center;
  -webkit-transition: .3s ease;
  transition: .3s ease;

}


.text:hover {
  cursor: pointer;
  -webkit-filter: opacity(.4);
          filter: opacity(.4);
  border-left-style: solid;
}

/************** EXP ***************/

.Experiences {
  height: 100vh;
  width: 100vw;
  background-color: #F2F2F2;

}

.EP-container {
  position: absolute;
  right: 0px;
  top:0px;
  height: 100vh;
  width: 45vw;
  text-align: center;
}

.EP-image {
  -webkit-filter:opacity(.3);
          filter:opacity(.3);
  -webkit-transition: .5s ease;
  transition: .5s ease;
  object-fit: fill;
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
}

.EP-overlay {
    visibility: visible;
    position: absolute;
    z-index:10;
    opacity: 1;
    height: 100%;
    width: 100%;
    -webkit-transition: .5s ease;
    transition: .5s ease;
}

.exp-content {
  padding: none;
  position: relative;

  width: 55vw;
  height:100%;
  /* display: flex;
  flex-direction: row; */
}

.exp { /*each exp block, image and desc*/
  width:100%;
  height: 30%;
  padding-top: 5%;
  text-align: center;
}

.exp-images { /*all images*/
  /* display: inline-block; */
  width:100%;
  max-height: 85vh;

}

.exp-img { /* an image*/
  max-width: 60%;
}

.exp-descriptions {
  position: relative;
  top:10%;
  width:45vw;
  max-height: 85vh;

}

.exp-desc {
  display: inline-block;
  max-width: 80%;
  text-align: left;

}
.exp-date {
  text-align: left;
  position: relative;
  padding-left:10%;
  padding-bottom: 2%;
  font-size:1.3em;
}

.header {
  font-size:3em;
  position: relative;
  padding-left: 15%;

}


